<template>
    <div class="params-view">
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>分类参数</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card>
            <!-- 头部的警告区域 -->
            <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" :closable="false" show-icon />

            <!-- 选择商品分类区域 -->
            <el-row class="cat-opt">
                <el-col>
                    <span>选择商品分类：</span>
                    <!-- 选择商品分类的级联选择框 -->
                    <el-cascader v-model="selectedCateIds" :options="cateList" :props="cascadeProps"
                        @change="handleChange" />
                </el-col>
            </el-row>

            <el-tabs v-model="activeName" @tab-change="handleTabChange">
                <!-- 添加动态参数的面板区域 -->
                <el-tab-pane label="动态参数" name="many">
                    <!-- 添加参数的按钮 -->
                    <el-button type="primary" :disabled="selectedCateIds.length !== 3"
                        @click="showAddParamsdialog">添加参数</el-button>
                    <!-- 动态参数的表格区域 -->
                    <el-table :data="manyTableData" border stripe row-key="attr_id">
                        <el-table-column type="expand" width="40px">
                            <template #default="scope">
                                <el-tag class="tableTag" v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{ item
                                }}</el-tag>
                                <!-- 输入的文本框 -->
                                <el-input v-if="scope.row.inputVisible" ref="inputRef" v-model="scope.row.inputValue"
                                    class="new-tag tableTag" size="small" @keyup.enter="handleInputConfirm(scope.row)"
                                    @blur="handleInputConfirm(scope.row)" />
                                <!-- 添加按钮 -->
                                <el-button v-else class="new-tag tableTag" size="small" @click="showInput(scope.row)">
                                    + New Tag
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column type="index" label="#" width="40px"></el-table-column>
                        <el-table-column label="参数名称" prop="attr_name"></el-table-column>
                        <el-table-column label="操作">
                            <template #default="scope">
                                <el-button type="primary" :icon="Edit" size="small"
                                    @click="showEidtParamsDialog(scope.row.attr_id)">编辑</el-button>
                                <el-button type="danger" :icon="Delete" size="small"
                                    @click="removeParams(scope.row.attr_id)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <!-- 添加静态属性的面版区域 -->
                <el-tab-pane label="静态属性" name="only">
                    <!-- 添加属性的按钮 -->
                    <el-button type="primary" :disabled="selectedCateIds.length !== 3"
                        @click="showAddParamsdialog">添加属性</el-button>
                    <!-- 静态属性的表格区域 -->
                    <el-table :data="onlyTableData" border stripe row-key="attr_id">
                        <el-table-column type="expand" width="40px">
                            <template #default="scope">
                                <el-tag class="tableTag" v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i, scope.row)">{{ item
                                }}</el-tag>
                                <!-- 输入的文本框 -->
                                <el-input v-if="scope.row.inputVisible" ref="inputRef" v-model="scope.row.inputValue"
                                    class="new-tag tableTag" size="small" @keyup.enter="handleInputConfirm(scope.row)"
                                    @blur="handleInputConfirm(scope.row)" />
                                <!-- 添加按钮 -->
                                <el-button v-else class="new-tag tableTag" size="small" @click="showInput(scope.row)">
                                    + New Tag
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column type="index" label="#" width="40px"></el-table-column>
                        <el-table-column label="属性名称" prop="attr_name"></el-table-column>
                        <el-table-column label="操作" min-width="164px">
                            <template #default="scope">
                                <el-button type="primary" :icon="Edit" size="small"
                                    @click="showEidtParamsDialog(scope.row.attr_id)">编辑</el-button>
                                <el-button type="danger" :icon="Delete" size="small"
                                    @click="removeParams(scope.row.attr_id)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <!-- 添加参数的对话框 -->
        <el-dialog v-model="addParamsDialogVisible" :title="'添加' + titleText" width="50%" @close="addParamsDialogClosed">
            <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px" status-icon>
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="addForm.attr_name" />
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addParamsDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="addParams">
                        确定
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 修改加参数的对话框 -->
        <el-dialog v-model="editParamsDialogVisible" :title="'修改' + titleText" width="50%" @close="editParamsDialogClosed">
            <el-form ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="80px" status-icon>
                <el-form-item :label="titleText" prop="attr_name">
                    <el-input v-model="editForm.attr_name" />
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="editParamsDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="editParams">
                        确定
                    </el-button>
                </span>
            </template>
        </el-dialog>

    </div>
</template>

<script>
import { ArrowRight, Search, Edit, Delete, Setting } from '@element-plus/icons-vue'
export default {
    data() {
        return {
            ArrowRight, Edit, Delete,
            // 所有商品分类的列表
            cateList: [],
            // 级联选择框的配置对象
            cascadeProps: {
                expandTrigger: 'hover',
                label: 'cat_name',
                value: 'cat_id',
                children: 'children'
            },
            // 级联选择框被选中的商品分类id数组
            selectedCateIds: [],
            // 被激活的页签的名称，
            // 默认值的设定可以控制刚进入页面是默认选中的页签
            activeName: 'many',
            // 动态参数的数据
            manyTableData: [],
            // 静态属性的数据
            onlyTableData: [],
            // 控制添加动态参数/静态属性对话框的显示与隐藏
            addParamsDialogVisible: false,
            // 添加参数的表单数据对象
            addForm: {},
            // 添加参数的表单验证规则对象
            addFormRules: {
                attr_name: [
                    { required: true, message: '请输入参数名称', trigger: 'blur' },
                ]
            },
            // 控制修改对话框的显示与隐藏
            editParamsDialogVisible: false,
            // 修改参数的表单数据对象
            editForm: {},
            // 修改参数的表单验证规则对象
            editFormRules: {
                attr_name: [
                    { required: true, message: '请输入参数名称', trigger: 'blur' },
                ]
            },
        }
    },
    created() {
        this.getCateList();
    },
    methods: {
        // 获取所有的商品分类列表
        getCateList() {
            this.$api.httpGet('categories', { showOuter: 'error' })
                .then(res => {
                    this.cateList = res.data;
                });

        },
        // 级联选择框选中变化时，触发函数
        handleChange() {
            // element-plus中默认只允许选中最后一级分类，当一级或二级分类没有子分类时，也可以被选中
            // 但我们需要只选中三级分类。
            // 只允许选中三级分类，即selectedCateIds数组的长度为3
            this.getParamsData();
        },
        // 页签切换时触发
        handleTabChange() {
            this.getParamsData();
        },
        // 获取参数的列表数据
        getParamsData() {
            // 长度不为3的情况下，直接清空数组即可。
            if (this.selectedCateIds.length !== 3) {
                this.selectedCateIds = [];
                this.manyTableData = [];
                this.onlyTableData = [];
                return;
            }

            // 如果没有return，则证明选择的是三级分类
            // 根据所选的商品分类id和当前所处的页签，获取对应的参数
            this.$api.httpGet('categories/' + this.cateId + '/attributes', { showOuter: false, error: ['获取分类参数失败！'] }, 200, { params: { sel: this.activeName } })
                .then(res => {
                    // 将attr_vals字符串转化为数组
                    res.data.forEach(item => {
                        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : [];
                        // 控制文本框的显示与隐藏
                        item.inputVisible = false;
                        // 文本框中输入的值
                        item.inputValue = '';
                    });
                    if (this.activeName === 'many') {
                        this.manyTableData = res.data;
                    } else {
                        this.onlyTableData = res.data;
                    }
                })
        },
        // 展示添加动态参数/静态属性的对话框
        showAddParamsdialog() {
            this.addParamsDialogVisible = true;
        },
        // 监听添加对话框的关闭事件
        addParamsDialogClosed() {
            this.$refs.addFormRef.resetFields();
        },
        // 点击按钮，添加参数
        addParams() {
            this.$refs.addFormRef.validate(valid => {
                if (!valid) return;

                this.$api.httpPost('categories/' + this.cateId + '/attributes', { attr_name: this.addForm.attr_name, attr_sel: this.activeName }, { showOuter: 'error', success: ['添加参数成功！'], error: ['添加参数失败！'] }, 201).then(() => {
                    this.addParamsDialogVisible = false;
                    this.getParamsData();
                })
            });
        },
        // 点击按钮，展示修改对话框
        showEidtParamsDialog(attr_id) {
            // 根据id查询参数
            this.$api.httpGet(`categories/${this.cateId}/attributes/${attr_id}`, { showOuter: 'error', error: ['获取参数信息失败！'] }, 200, { params: { attr_sel: this.activeName } })
                .then(res => {
                    this.editForm = res.data;
                    this.editParamsDialogVisible = true;
                });
        },
        // 关闭修改对话框前，重置表单
        editParamsDialogClosed() {
            this.editForm = {};
            this.$refs.editFormRef.resetFields();
        },
        // 点击按钮，修改参数信息
        editParams() {
            this.$refs.editFormRef.validate(valid => {
                if (!valid) return;
                this.$api.httpPut(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`, { attr_name: this.editForm.attr_name, attr_sel: this.editForm.attr_sel }, { showOuter: 'error', success: ['修改参数成功！'], error: ['修改参数失败！'] })
                    .then(() => {
                        // 参数修改成功，关闭对话框，刷新页面数据
                        this.editParamsDialogVisible = false;
                        this.getParamsData();
                    })
            });
        },
        // 根据id删除对应参数
        removeParams(attr_id) {
            this.$confirm(
                '此操作将永久删除该参数，是否继续？',
                '删除参数',
                {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }
            )
                .then(() => {
                    return this.$api.httpDelete(`categories/${this.cateId}/attributes/${attr_id}`, { error: ['删除失败！'] })
                })
                .then(() => {
                    // 删除参数成功，刷新列表
                    this.getParamsData();
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '删除取消！',
                        duration: 1200,
                    })
                })
        },
        // 文本框失去焦点 或 按下enter键 时 触发
        handleInputConfirm(row) {
            // 如果为输入文字，或输入的都是空格，清空输入
            if (row.inputValue.trim().length === 0) {
                row.inputValue = '';
                row.inputVisible = false;
                return;
            }
            // 如果没有结束，则对输入内容进行后续处理
            // 将用户输入的值添加到row.attr_vals数组中
            row.attr_vals.push(row.inputValue.trim());
            row.inputValue = '';
            row.inputVisible = false;
            // 发起请求，将输入的值持久化
            this.saveAttrVals(row);
        },
        saveAttrVals(row) {
            // 发起请求，将输入的值持久化
            this.$api.httpPut(`categories/${this.cateId}/attributes/${row.attr_id}`, 
            {
                attr_name: row.attr_name, 
                attr_sel: row.attr_sel, 
                attr_vals: row.attr_vals.join(' ')
            }, 
            {
                showOuter: 'error', 
                success: ['可选项修改成功！'], 
                error: ['可选项修改失败！']
            });
        },
        // 点击按钮，按钮隐藏，显示输入框
        showInput(row) {
            row.inputVisible = true;
            // $nextTick() 方法的作用 当前页面上元素被重新渲染之后，才会执行回调函数中的代码
            this.$nextTick(() => {
                this.$refs.inputRef.$refs.input.focus();
            });
        },
        // 删除对应的参数可选项
        handleClose(i, row) {
            row.attr_vals.splice(i, 1);
            // 发起请求，保存这次操作
            this.saveAttrVals(row);
        }
    },
    computed: {
        // 当前选中的三级分类的id
        cateId() {
            if (this.selectedCateIds.length === 3) return this.selectedCateIds[2];
            return null;
        },
        // 动态参数/静态属性
        titleText() {
            if (this.activeName === 'many') {
                return '动态参数';
            } else {
                return '静态属性';
            }
        }
    }
}
</script>

<style lang="less" scoped>
.cat-opt {
    margin: 15px 0;
}

.tableTag {
    margin: 10px;
}

.new-tag {
    width: 90px;
}
</style>