<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb :separator-icon="ArrowRight">
            <el-breadcrumb-item :to="{ path: '/home/welcome' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品分类</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片试图区域 -->
        <el-card>
            <!-- 添加按钮区域 -->
            <el-row>
                <el-col>
                    <el-button type="primary" @click="showAddCateDialog">添加分类</el-button>
                </el-col>
            </el-row>
            <!-- 表格区域 -->
            <el-table :data="cateList" border row-key="cat_id" lazy :load="lazyLoad">
                <el-table-column type="index" width="72px" label="#" :index="getIndex"></el-table-column>
                <el-table-column label="分类名称" prop="cat_name" min-width="174px"></el-table-column>
                <el-table-column label="是否有效" width="73px">
                    <template #default="scope">
                        <CircleCheckFilled class="icons success" v-if="scope.row.cat_deleted" />
                        <CircleCloseFilled class="icons error" v-else />
                    </template>
                </el-table-column>
                <el-table-column label="排序">
                    <template #default="scope">
                        <el-tag v-if="scope.row.cat_level === 0" size="small">一级</el-tag>
                        <el-tag v-if="scope.row.cat_level === 1" size="small" type="success">二级</el-tag>
                        <el-tag v-if="scope.row.cat_level === 2" size="small" type="warning">三级</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" min-width="182px">
                    <template #default="scope">
                        <!-- 修改按钮 -->
                        <el-button type="primary" :icon="Edit" size="small"
                            @click="showSetCateDialog(scope.row.cat_id)">编辑</el-button>
                        <!-- 删除按钮 -->
                        <el-button type="danger" :icon="Delete" size="small"
                            @click="removeCateById(scope.row.cat_id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页区域 -->
            <el-pagination v-model:current-page="queryInfo.pagenum" v-model:page-size="queryInfo.pagesize"
                :page-sizes="[5, 10, 20, 50, 100]" small background layout="total, sizes, prev, pager, next, jumper"
                :total="total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
        </el-card>
        <!-- 添加分类的对话框 -->
        <el-dialog title="添加分类" width="50%" v-model="addCateDialogVisible" @close="addCateDialogClosed">
            <!-- 添加分类的表单区域 -->
            <el-form :model="addCateForm" label-width="100px" :rules="addCateFormRules" ref="addCateFormRef">
                <el-form-item label="分类名称：" prop="cat_name">
                    <el-input v-model="addCateForm.cat_name" />
                </el-form-item>
                <el-form-item label="父级分类：">
                    <el-cascader style="width: 100%;" v-model="selectedParentCateIds" :options="parentCateList"
                        :props="cascaderProps" @change="parentCateChanged" clearable />
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="addCateDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="addCate">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
        <!-- 编辑分类的对话框 -->
        <el-dialog title="编辑分类" width="50%" v-model="setCateDialogVisible" @close="setCateDialogClosed">
            <!-- 添加分类的表单区域 -->
            <el-form :model="setCateForm" label-width="100px" :rules="setCateFormRules" ref="setCateFormRef">
                <el-form-item label="分类名称：" prop="cat_name">
                    <el-input v-model="setCateForm.cat_name" />
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="setCateDialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="setCate">
                        确认
                    </el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>
import { ArrowRight, CircleCheckFilled, CircleCloseFilled, Edit, Delete, Setting } from '@element-plus/icons-vue'
export default {
    data() {
        return {
            ArrowRight, CircleCheckFilled, CircleCloseFilled, Edit, Delete, Setting,
            // 商品分类的数据列表，默认为空
            cateList: [],
            // 商品分类列表的查询条件
            queryInfo: {
                type: 3,
                pagenum: 1,
                pagesize: 5
            },
            // 商品分类列表条目的总数
            total: 0,
            // 通过递归获取的商品分类表的索引
            indexList: [],
            // 控制添加分类的对话框的显示与隐藏
            addCateDialogVisible: false,
            // 添加商品分类的表单数据对象
            addCateForm: {
                // 分类父id
                cat_pid: 0,
                // 分类名称
                cat_name: '',
                // 分类层级
                cat_level: 0
            },
            // 添加商品分类的校验规则
            addCateFormRules: {
                cat_name: [
                    { required: true, message: '请输入分类名称', triggle: 'blur' },
                ],
            },
            // 父级分类的列表
            parentCateList: [],
            // 级联选择器的配置对象
            cascaderProps: {
                value: 'cat_id',
                label: 'cat_name',
                children: 'children',
                // 选中的方式设定为hover
                expandTrigger: 'hover',
                // 让父子选项失去关联，可以只选择任意一级选项
                checkStrictly: true,
            },
            // 选中的父级分类的id
            selectedParentCateIds: [],
            // 控制编辑分类的对话框的显示与隐藏
            setCateDialogVisible: false,
            // 编辑商品分类的表单数据对象
            setCateForm: {
                // 分类名称
                cat_name: ''
            },
            // 编辑商品分类的表单数据对象的备份
            setCateFormBackup: {
                // 分类名称
                cat_name: ''
            },
            // 编辑商品分类的校验规则
            setCateFormRules: {
                cat_name: [
                    { required: true, message: '请输入分类名称', triggle: 'blur' },
                ],
            },

        }
    },
    created() {
        this.getCateList();
    },
    methods: {
        // 获取商品分类列表
        async getCateList() {
            const res = await this.$api.httpGet('categories', { showOuter: 'error', error: ['商品分类数据获取失败!'] }, 200, { params: this.queryInfo });
            this.cateList = res.data.result;
            this.total = res.data.total;
            this.indexList = [];
            this.getTableIndex(this.cateList, this.indexList, '');
        },
        // 使用递归获取商品分类表的索引
        getTableIndex(arr, indexList, sufIndex) {
            for (let i = 0; i < arr.length; i++) {
                indexList.push(sufIndex + (i + 1));
                if (arr[i].children) {
                    // arr[i].hasChildren = 'hasChildren';
                    let sufIndex = indexList[indexList.length - 1] + '-';
                    this.getTableIndex(arr[i].children, indexList, sufIndex);
                }
            }
        },
        // 为商品分类列表分配索引
        getIndex(num) {
            return this.indexList[num];
        },
        // 商品分类列表的懒加载函数
        lazyLoad(row, treeNode, resolve) {
            // 此处虽然添加了懒加载，但是实际上所有的数据都已经拿到了
            // 因此，此处的懒加载实际上没有任何意义。
            // 如果想实现真实的懒加载
            // 就应该是先请求第一级的商品分类清单
            // 在点击扩展按钮时，也就是此处，resolve之前，临时发起异步请求获取二级列表的数据
            // 将二级列表的数据通过resolve函数呈现
            // 这才是真正的懒加载的实现步骤
            setTimeout(() => {
                resolve(row.children);
            }, 0);
        },
        // 监听 pagesize 改变的事件
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize;
            this.getCateList();
        },
        // 监听 pagenum 改变的事件
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage;
            this.getCateList();
        },
        // 点击按钮，展示添加分类的对话框
        showAddCateDialog() {
            this.getParentCateList();
            this.addCateDialogVisible = true;
        },
        // 获取父级分类的数据列表
        async getParentCateList() {
            const res = await this.$api.httpGet('categories', { showOuter: 'error', error: ['获取商品分类列表失败！'] }, 200, { params: { type: 2 } });
            this.parentCateList = res.data;
        },
        // 级联选择器的选择项发生变化时触发
        parentCateChanged() {
            Array.isArray(this.selectedParentCateIds) ? '' : this.selectedParentCateIds = [];
            if (this.selectedParentCateIds.length > 0) {
                this.addCateForm.cat_level = this.selectedParentCateIds.length;
                this.addCateForm.cat_pid = this.selectedParentCateIds[this.selectedParentCateIds.length - 1];
            } else {
                this.addCateForm.cat_level = 0;
                this.addCateForm.cat_pid = 0;
            }
        },
        // 点击确认按钮，添加新的分类
        addCate() {
            this.$refs.addCateFormRef.validate(valid => {
                if (!valid) return;
                this.$api.httpPost('categories', this.addCateForm, { error: ['分类添加失败！'] }, 201)
                    .then(() => {
                        this.getCateList();
                        this.addCateDialogVisible = false;
                    })
            })
        },
        // 监听对话框的关闭事件，重置表单数据
        addCateDialogClosed() {
            this.$refs.addCateFormRef.resetFields();
            this.selectedParentCateIds = [];
            this.addCateForm.cat_level = 0;
            this.addCateForm.cat_pid = 0;
        },
        // 点击按钮，展示编辑分类的对话框
        async showSetCateDialog(id) {
            await this.getCateById(id);
            this.setCateDialogVisible = true;
        },
        // 根据id查询分类信息
        async getCateById(id) {
            await this.$api.httpGet('categories/' + id, { showOuter: 'error', error: ['查询分类信息失败！'] })
                .then(res => {
                    this.setCateForm = res.data;
                    this.setCateFormBackup = { ...res.data };
                })
        },
        // 监听对话框的关闭事件，重置表单数据
        setCateDialogClosed() {
            this.$refs.setCateFormRef.resetFields();
            this.setCateForm.cat_name = '';
        },
        // 点击确认按钮，编辑分类
        setCate() {
            this.$refs.setCateFormRef.validate(valid => {
                if (!valid) return;
                // 判断分类名称是否被修改
                if (this.setCateForm.cat_name === this.setCateFormBackup.cat_name) return this.$message({ message: '分类名称未修改', duration: 1200 });

                this.$api.httpPut('categories/' + this.setCateForm.cat_id, this.setCateForm, { error: ['分类添加失败！'] })
                    .then(() => {
                        this.getCateList();
                        this.setCateDialogVisible = false;
                    })
            })
        },
        // 点击删除按钮，弹出删除的确认框
        removeCateById(id) {
            this.$confirm(
                '此操作将永久删除该商品分类，是否继续？',
                '删除',
                {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消',
                    type: 'warning',
                }
            ).then(() => {
                this.$api.httpDelete('categories/' + id, {error: ['删除失败！']})
                .then(() => {
                    this.getCateList();
                });
            }).catch(() => {
                this.$message({message: '删除取消', duration: 1200});
            });
        }
    }
}
</script>

<style lang="less" scoped>
.icons {
    width: 12px;
    height: 12px;
}

.success {
    color: light-green;
}

.error {
    color: red;
}
</style>